<template>
  <table class="table table-bordered">
    <thead>
      <tr>
        <th>Client ID</th>
        <th>Name</th>
        <th>From</th>
        <th>To</th>
      </tr>
    </thead>

    <tbody>
      <tr
        v-for="row in transactionRows"
        :key="row.clientId"
      >
        <td>{{ row.id }}</td>
        <td>{{ row.name }}</td>
        <td>{{ row.from }}</td>
        <td>{{ row.to }}</td>
      </tr>
    </tbody>
  </table>
</template>


<script>
export default {
  computed: {
    transactionRows() {
      return this.$store.getters['xt/getTransactionRows']
    },
  },
}
</script>



