<template>
  <div class="container-fluid">

    <div
      class="alert alert-danger text-center pb-3"
      v-if="errMsg"
    >
      {{ errMsg }}
    </div>

    <div class="row py-3">
      <div class="col">
        <h1 class="text-center">Exchange transaction</h1>
      </div>
    </div>
    <form @submit.prevent="createTransactions">
      <div class="row">
        <div class="col">

          <div class="row">

            <!-- From asset -->
            <div class="col-md-6 col-lg-4 col-xl-4 pt-3">
              <div class="form-group">
                <label>From asset</label>
                <b-form-select
                  v-model="fromAsset"
                  :options="ownedAssets"
                  class="form-control-sm"
                  :class="[{ 'is-invalid': !$v.fromAsset.required && $v.fromAsset.$dirty }]"
                  @change="$v.fromAsset.$touch()"
                >
                  <template slot="first">
                    <option :value="null">Please select currency</option>
                  </template>
                </b-form-select>
                <div class="invalid-feedback">Please, select source currency.</div>
              </div>
            </div>

            <!-- Date -->
            <div class="col-md-6 col-lg-4 col-xl-4 pt-3">
              <div class="form-group">
                <label>Date</label>
                <input
                  type="text"
                  class="form-control form-control-sm"
                  @input="setTransactionDate($event.target.value)"
                  :value="transactionDate"
                >
              </div>
            </div>

            <!-- Ref No. -->
            <div class="col-md-6 col-lg-4 col-xl-4 pt-3">
              <div class="form-group">
                <label>Start transaction id</label>
                <input
                  type="number"
                  step="1"
                  min="0"
                  class="form-control form-control-sm"
                  @input="setTransactionStartId($event.target.value)"
                  :value="transactionStartId"
                >
              </div>
            </div>

          </div>
        </div>
      </div>
      <div class="row">

        <!-- To assets -->
        <div class="col-md-6 col-lg-6 col-xl-6 pt-3">
          <div class="form-group">
            <label>To assets</label>
            <b-form-select
              multiple
              v-model="toAssets"
              :options="allAssets"
              class="form-control-sm"
              :class="[{ 'is-invalid': !$v.toAssets.required && $v.toAssets.$dirty }]"
              @change="$v.toAssets.$touch()"
            ></b-form-select>
            <div class="invalid-feedback">Please, select target assets.</div>
          </div>
        </div>

        <!-- Clients -->
        <div class="col-md-6 col-lg-6 col-xl-4 pt-3">
          <div class="form-group">
            <label>Clients</label>
            <b-form-select
              multiple
              v-model="selectedClients"
              :options="clients"
              class="form-control-sm"
              :class="[{ 'is-invalid': !$v.selectedClients.required && $v.selectedClients.$dirty }]"
              @change="$v.selectedClients.$touch()"
            ></b-form-select>
            <div class="invalid-feedback">Please, select clients.</div>
          </div>
        </div>

      </div>

      <div class="row">

        <!-- amounts -->
        <div
          class="col pt-3"
          v-if="fromAsset"
        >
          <div class="row">
            <div class="col">
              <h5>Input transactions</h5>
            </div>
            <div class="col text-right">{{totalValue | num}} {{fromAsset}}</div>
            <div class="col text-right"></div>
          </div>
          <table class="table table-sm table-responsive table-striped w-100 text-right">
            <thead>
              <tr>
                <th>Asset</th>
                <th title="state before transactions">Before</th>
                <th title="spent from source asset">Spent {{fromAsset}}</th>
                <th title="bought from target asset">Bought</th>
                <th title="exchange fees">X Fee</th>
                <th title="conversion ration / fixing">FX</th>
                <th title="blockchain fees">B Fee</th>
                <th title="state after transactions">After</th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="asset in selectedAssets"
                :key="asset"
                :class="{'table-danger': inputTransactions[asset].error, 'table-warning': inputTransactions[asset].warning}"
              >
                <th class="text-center">{{asset}}</th>
                <td class="text-right">{{inputTransactions[asset].before | num}}</td>
                <td>
                  <div
                    v-if="asset !== fromAsset"
                    class="input-group"
                  >
                    <div class="input-group-addon">
                      <span class="input-group-text">{{fromAsset}}</span>
                    </div>
                    <input
                      class="form-control text-right w-100"
                      type="number"
                      min="0"
                      step="any"
                      :value="inputTransactions[asset].spent"
                      @input="setInputTxValue({ticker: asset, spent: $event.target.value})"
                    >
                  </div>
                  <span
                    v-else
                    class="text-muted"
                  >N/A</span>
                </td>
                <td class="text-right">
                  <div
                    v-if="asset !== fromAsset"
                    class="input-group"
                  >
                    <div class="input-group-addon">
                      <span class="input-group-text">{{asset}}</span>
                    </div>
                    <input
                      class="form-control text-right w-100"
                      type="number"
                      min="0"
                      step="any"
                      :value="inputTransactions[asset].bought"
                      @input="setInputTxValue({ticker: asset, bought: $event.target.value})"
                    >
                  </div>
                  <span v-else>{{inputTransactions[asset].bought | num}}</span>
                </td>
                <td>
                  <div class="input-group">
                    <div class="input-group-addon">
                      <span class="input-group-text">{{asset}}</span>
                    </div>
                    <input
                      class="form-control text-right w-100"
                      type="number"
                      min="0"
                      step="any"
                      :value="inputTransactions[asset].exchangeFees"
                      @input="setInputTxValue({ticker: asset, exchangeFees: $event.target.value})"
                    >
                  </div>
                </td>
                <td>{{inputTransactions[asset].exchangeRate | num}}</td>
                <td>
                  <div class="input-group">
                    <div class="input-group-addon">
                      <span class="input-group-text">{{asset}}</span>
                    </div>
                    <input
                      class="form-control text-right w-100"
                      type="number"
                      min="0"
                      step="any"
                      :value="inputTransactions[asset].blockchainFees"
                      @input="setInputTxValue({ticker: asset, blockchainFees: $event.target.value})"
                    >
                  </div>
                </td>
                <td>{{inputTransactions[asset].after | num}}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>

      <div class="row">
        <div class="col pt-3">

          <div class="list-group">
            <div
              class="list-group-item"
              v-for="client in selectedClients"
              :key="client.id"
            >
              <div class="col pt-3">
                <div class="row">
                  <div class="col">
                    <h5>{{client.name}}</h5>
                  </div>
                  <div
                    class="col text-right"
                    v-if="!isNaN(clientWeights[client.id].totalInput)"
                  >
                    {{clientWeights[client.id].totalInput | num}} {{fromAsset}}
                  </div>
                  <div
                    class="col text-right"
                    v-if="!isNaN(clientWeights[client.id].weight)"
                  >
                    {{clientWeights[client.id].weight
                    | percent}}
                  </div>
                </div>
                <table class="table table-sm table-striped text-right">
                  <thead>
                    <tr>
                      <th>Asset</th>
                      <th>Before</th>
                      <th>Spent {{fromAsset}}</th>
                      <th>Bought</th>
                      <th>FX</th>
                      <th>B Fee</th>
                      <th>After</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr
                      v-for="asset in selectedAssets"
                      :key="asset"
                      :class="{'table-danger': clientRows[client.id][asset].error, 'table-warning': clientRows[client.id][asset].warning}"
                    >
                      <th class="text-center">{{asset}}</th>
                      <td>{{clientRows[client.id][asset].before | num}}</td>
                      <td>
                        <span v-if="asset !== fromAsset">
                          {{clientRows[client.id][asset].spent | num}}
                        </span>
                        <span
                          v-else
                          class="text-muted"
                        >
                          N/A
                        </span>
                      </td>
                      <td>{{clientRows[client.id][asset].bought | num}}</td>
                      <td>{{clientRows[client.id][asset].exchangeRate | num}}</td>
                      <td>{{clientRows[client.id][asset].blockchainFees | num}}</td>
                      <td>{{clientRows[client.id][asset].after | num}}</td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>

        </div>
      </div>

      <div
        class="row"
        v-if="transactionRows.length > 0"
      >
        <div class="col py-3">
          <table class="table table-striped">
            <thead>
              <tr>
                <th v-for="(value, key) in transactionRows[0]">{{key}}</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="row in transactionRows">
                <td v-for="col in row">
                  {{col}}
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>

      <div class="row py-3">
        <div class="col-lg-3">
          <button
            type="submit"
            class="btn btn-lg btn-block"
            :class="{'btn-success': canSubmit, 'btn-outline-success': !canSubmit}"
            :disabled="!canSubmit"
          >
            Submit transaction (TODO)
          </button>
        </div>
      </div>
    </form>

    <loading :loading="loading"></loading>

    <b-modal
      v-model="showModal"
      title="Successful submit"
      header-bg-variant="success"
      header-text-variant="light"
    >
      <div slot="modal-header-close"></div>
      <p>The transactions were submitted successfully.</p>
      <div
        slot="modal-footer"
        class="w-100"
      >
        <b-btn
          size="sm"
          class="float-right"
          variant="primary"
          @click="showModal=false"
          to="/"
        >OK</b-btn>
      </div>
    </b-modal>
  </div>
</template>


<script>
import ExchangeTransactionTable from '../components/tables/ExchangeTransactionTable'
import { required } from 'vuelidate/lib/validators'
import { mapGetters, mapActions, mapMutations, mapState } from 'vuex'
import Big from 'big.js'
import uploadService from '../services/uploadService'

export default {
  components: {
    ExchangeTransactionTable,
  },
  validations: {
    fromAsset: {
      required,
    },
    toAssets: {
      required,
    },
    selectedClients: {
      required,
    },
    fromAmount: {
      required,
    },
    toAmount: {
      required,
    },
  },
  created() {
    this.loading = true
    this.loadData().finally(() => {
      this.loading = false
    })
  },
  data() {
    return {
      loading: false,
      showModal: false,
      errMsg: null,
    }
  },
  filters: {
    num(value) {
      try {
        return new Big(value).toFixed(12)
      } catch (e) {
        return value
      }
    },
    percent(value) {
      try {
        return `${new Big(value).mul(100).toFixed(4)}%`
      } catch (e) {
        return value
      }
    },
  },
  computed: {
    ...mapState('xt', [
      'transactionDate', 'transactionStartId',
    ]),
    ...mapGetters('xt', {
      storeOwnedAssets: 'getOwnedAssets',
      storeAllAssets: 'getAllAssets',
      storeClients: 'getClients',
      storeFromAsset: 'getFromAsset',
      storeToAssets: 'getToAssets',
      storeFromAmount: 'getFromAmount',
      storeToAmount: 'getToAmount',
      storeSelectedClients: 'getSelectedClients',
      storeSelectedClientsAmount: 'getSelectedClientsAmount',
      storeBeforeAmounts: 'getBeforeAmounts',
      inputTransactions: 'getInputTransactions',
      selectedAssets: 'getSelectedAssets',
      clientsAssets: 'getClientsAssets',
      clientRows: 'getClientRows',
      clientWeights: 'getClientWeights',
      totalValue: 'getTotalValue',
      canSubmit: 'canSubmit',
      transactionRows: 'getTransactionRows',
    }),
    clients() {
      return this.storeClients && this.storeClients.map((client) => ({
        value: client,
        text: client.name,
      }))
    },
    ownedAssets() {
      return (
        this.storeOwnedAssets && this.storeOwnedAssets.map(({ ticker, name, pieces }) => ({
          value: ticker,
          text: `${name}(${ticker}) ${pieces}`,
        }))
      )
    },
    allAssets() {
      return (
        this.storeAllAssets && this.storeAllAssets.map(({ ticker, name }) => ({
          value: ticker,
          text: `${name}(${ticker}) ${this.storeOwnedAssets.reduce((res, { ticker: t, pieces }) => res || (t === ticker ? pieces : ''), '')}`,
        })).filter(({ value }) => value !== this.fromAsset)
      )
    },
    fromAsset: {
      get() {
        return this.storeFromAsset
      },
      set(val) {
        this.setFromAsset(val)
      },
    },
    toAssets: {
      get() {
        return this.storeToAssets
      },
      set(val) {
        this.setToAssets(val)
      },
    },
    fromAmount: {
      get() {
        return this.storeFromAmount
      },
      set(val) {
        this.setFromAmount(val)
      },
    },
    toAmount: {
      get() {
        return this.storeToAmount
      },
      set(val) {
        this.setToAmount(val)
      },
    },
    selectedClients: {
      get() {
        return this.storeSelectedClients
      },
      set(val) {
        this.setSelectedClients(val)
      },
    },
  },
  methods: {
    ...mapActions('xt', ['loadData']),
    ...mapMutations('xt', [
      'setFromAsset', 'setToAssets', 'setSelectedClients', 'setInputTxValue',
      'setTransactionDate', 'setTransactionStartId',
    ]),
    createTransactions() {
      this.loading = true

      uploadService.uploadJSON({ data: this.transactionRows })
        .then(() => {
          this.showModal = true
        })
        .catch(errRes => {
          this.errMsg = errRes.message
        })
        .finally(() => {
          this.loading = false
        })
    },
  },
}
</script>



