import axios from 'axios'
import dateUtils from '@/helpers/dateUtils'
import {apiToPrice} from './converters'

const API_V1_URL = process.env.API_V1_URL

export default {
  listPrices({date, q, limit = 1000, offset = 0} = {}) {
    return axios.get(`${API_V1_URL}/prices/${date ? dateUtils.formatDate(new Date(date)) : ''}`, {
      params: {
        q,
        limit,
        offset,
      },
    })
      .then(res => res.data.map(apiToPrice))
      .catch(errObj => Promise.reject(errObj.response.data.error))
  },
}



