import portfolioService from '../../services/portfolioService'
import userService from '../../services/userService'
import priceService from '../../services/priceService'
import Big from 'big.js'
import dateUtils from '@/helpers/dateUtils'

const state = {
  transactionRows: [],
  clients: [],
  ownedAssets: [],
  allAssets: [],
  clientsAssets: [],
  fromAsset: null,
  toAssets: [],
  fromAmount: null,
  toAmount: null,
  selectedClients: [],
  inputTransactions: {},
  transactionDate: dateUtils.formatDate(new Date()),
  transactionStartId: 1,
}

const computeInputTransaction = (...params) => ([
  ({before, blockchainFees = 0, bought, exchangeFees = 0}) => ({
    after: new Big(before).plus(bought).minus(exchangeFees).minus(blockchainFees),
  }),
  ({spent, bought, exchangeFees = 0}) => ({
    exchangeRate: new Big(spent).div(new Big(bought).minus(exchangeFees)),
  }),
  ({blockchainFees}) => ({
    warning: !blockchainFees,
  }),
  ({after}) => ({
    error: !after || new Big(after).lt(0),
  }),
].reduce(
  (tx, op) => {
    try {
      const oped = op(tx)
      return {...tx, ...oped}
    } catch (err) {
      return tx
    }
  },
  params.reduce((tx, param) => ({...tx, ...param}), {})
))

const computeClientRow = (row, ...params) => ([
  ({before = 0}) => ({before}),
  ({weight}, {after}) => ({
    after: new Big(weight).mul(after),
  }),
  ({weight}, {blockchainFees = 0}) => ({
    blockchainFees: new Big(weight).mul(blockchainFees),
  }),
  ({after, blockchainFees = 0, before = 0}, {exchangeRate}) => ({
    bought: new Big(after).minus(before).plus(blockchainFees),
  }),
  ({bought}, {exchangeRate}) => ({
    spent: new Big(bought).mul(exchangeRate),
  }),
  ({bought, spent}) => ({
    exchangeRate: new Big(spent).div(bought),
  }),
  ({after}) => ({
    error: !after || new Big(after).lt(0),
  }),
].reduce(
  (row, op) => {
    try {
      return {...row, ...op(row, ...params)}
    } catch (err) {
      return row
    }
  },
  row
))

const getters = {
  getClientWeights({clientsAssets, selectedClients}, {getInputTransactions: inputTransactions}) {
    const weights = selectedClients.reduce((weights, {id}) => {
      return ({
        ...weights,
        [id]: {
          totalInput: Object
            .keys(clientsAssets[id])
            .filter(ticker => inputTransactions[ticker])
            .reduce(
              (sum, ticker) =>
                inputTransactions[ticker].exchangeRate && !isNaN(sum)
                  ? new Big(clientsAssets[id][ticker])
                    .mul(inputTransactions[ticker].exchangeRate)
                    .plus(sum)
                  : NaN,
              0
            ),
        },
      })
    }, {})
    const totalInputs = Object.keys(weights).reduce(
      (sum, id) =>
        (isNaN(weights[id].totalInput) || isNaN(sum))
          ? NaN
          : weights[id].totalInput.plus(sum),
      0
    )
    return Object.keys(weights).reduce((res, id) => ({
      ...res,
      [id]: {
        ...weights[id],
        weight: isNaN(totalInputs) ? NaN : weights[id].totalInput.div(totalInputs),
      },
    }), {})
  },
  getClientRows(
    {
      fromAsset,
      clientsAssets,
      selectedClients,
    }, {
      getInputTransactions: inputTransactions,
      getSelectedAssets: selectedAssets,
      getClientWeights: clientWeights,
    }
  ) {
    return selectedClients.reduce((res, {id}) => ({
      ...res,
      [id]: selectedAssets.reduce((res, ticker) => ({
        ...res,
        [ticker]: computeClientRow({
          before: clientsAssets[id][ticker],
          ...clientWeights[id],
        }, inputTransactions[ticker]),
      }), {}),
    }), {})
  },
  getInputTransactions({inputTransactions, fromAsset}, {getBeforeAmounts, getSelectedAssets}) {
    const inputs = getSelectedAssets.reduce((rows, ticker) => ({
      ...rows,
      [ticker]: computeInputTransaction({
        ...inputTransactions[ticker],
        before: getBeforeAmounts[ticker],
      }, ticker === fromAsset ? {exchangeRate: 1} : {}),
    }), {})
    if (fromAsset) {
      inputs[fromAsset].bought = Object
        .keys(inputs)
        .filter(ticker => ticker !== fromAsset)
        .reduce((sum, ticker) => sum.minus(inputs[ticker].spent || 0), new Big(0))
      inputs[fromAsset].spent = inputs[fromAsset].bought
      inputs[fromAsset] = computeInputTransaction(inputs[fromAsset])
    }
    return inputs
  },
  isValid({fromAmount, fromAsset, toAmount, toAssets}, {getSelectedClientsAmount}) {
    if (!fromAmount || !fromAsset || !toAmount || !toAssets) {
      return false
    }
    if (getSelectedClientsAmount < fromAmount) {
      return false
    }
    if (toAssets.includes(fromAsset)) {
      return false
    }
    return true
  },
  getTotalValue(store, {getInputTransactions: inputTransactions}) {
    return Object.keys(inputTransactions).reduce((sum, ticker) => new Big(inputTransactions[ticker].exchangeRate || 0).mul(inputTransactions[ticker].before || 0).plus(sum), 0)
  },
  getSelectedAssets({fromAsset, toAssets}) {
    return [fromAsset, ...toAssets].filter(Boolean)
  },
  getBeforeAmounts({fromAsset, clientsAssets, selectedClients, toAssets}, {getSelectedAssets}) {
    return getSelectedAssets.reduce((assets, ticker) => ({
      ...assets,
      [ticker]: selectedClients.reduce((sum, {id}) => new Big(sum).plus(clientsAssets[id][ticker] || 0), 0),
    }), {})
  },
  getSelectedClientsAmount({selectedClients, clientsAssets, fromAsset}) {
    if (!fromAsset) {
      return
    }
    return selectedClients.reduce((sum, {id}) => sum + parseFloat(clientsAssets[id][fromAsset]), 0)
  },
  getOwnedAssets(state) {
    return state.ownedAssets
  },
  getAllAssets(state) {
    return state.allAssets
  },
  getClients({clients, clientsAssets}, {getSelectedAssets: assets}) {
    return clients
      .filter(({id}) =>
        assets.some((ticker) => clientsAssets[id][ticker])
      )
      .sort(({name: a}, {name: b}) => a.localeCompare(b))
  },
  getClientsAssets(state) {
    return state.clientsAssets
  },
  getFromAsset(state) {
    return state.fromAsset
  },
  getToAssets(state) {
    return state.toAssets
  },
  getFromAmount(state) {
    return state.fromAmount
  },
  getToAmount(state) {
    return state.toAmount
  },
  getSelectedClients(state) {
    return state.selectedClients
  },
  canSubmit({fromAsset, toAssets, selectedClients}, {getInputTransactions: inputTransactions, getClientRows: clientRows}) {
    return (
      fromAsset &&
      toAssets.length &&
      selectedClients.length &&
      Object.keys(inputTransactions).reduce((success, asset) => success && !inputTransactions[asset].error, true) &&
      Object.keys(clientRows).reduce((success, id) => success && Object.keys(clientRows[id]).reduce((success, asset) => success && !clientRows[id][asset].error, true), true)
    )
  },
  getClientsById({clients}) {
    return clients.reduce((map, client) => {
      map[client.id] = client
      return map
    }, {})
  },
  getTransactionRows({transactionDate, transactionStartId, fromAsset, selectedClients}, {getClientRows: clientRows, getClientsById: clients}) {
    const rows = []
    const precision = 16
    const zero = new Big(0).toFixed(precision)
    try {
      for (let id in clientRows) {
        if (!clientRows.hasOwnProperty(id)) {
          continue
        }

        let add = (props) => rows.push({
          Date: transactionDate,
          Counterparty: clients[id].name,
          'Client ID': clients[id].clientId,
          'Transaction ID': transactionStartId++,
          'Portfolio ID': clients[id].portfolios[0],
          ...props,
        })

        for (let asset in clientRows[id]) {
          if (!clientRows[id].hasOwnProperty(asset)) {
            continue
          }
          const row = clientRows[id][asset]
          if (asset !== fromAsset) {
            add({
              Type: 'change',
              'Currency 1 Amount': new Big(row.bought).toFixed(16),
              'Currency 1': asset,
              'Currency 2 Amount': new Big(row.spent).mul(-1).toFixed(16),
              'Currency 2': fromAsset,
            })
          }
          if (new Big(row.blockchainFees).toFixed(precision) !== zero) {
            add({
              Type: 'blockchain fee',
              'Currency 1 Amount': new Big(row.blockchainFees).mul(-1).toFixed(16),
              'Currency 1': asset,
              'Currency 2 Amount': null,
              'Currency 2': null,
            })
          }
        }
      }
      return rows
    } catch (e) {
      return []
    }
  },
}

const mutations = {
  setTransactionRows(state, payload) {
    state.transactionRows = payload
  },
  setOwnedAssets(state, payload) {
    state.ownedAssets = payload
  },
  setAllAssets(state, payload) {
    state.allAssets = payload
  },
  setClients(state, payload) {
    state.clients = payload
  },
  setClientsAssets(state, payload) {
    state.clientsAssets = payload
  },
  setFromAsset(state, payload) {
    state.fromAsset = payload
    state.selectedClients = []
  },
  setToAssets(state, payload) {
    state.toAssets = payload
  },
  setFromAmount(state, payload) {
    state.fromAmount = payload
  },
  setToAmount(state, payload) {
    state.toAmount = payload
  },
  setSelectedClients(state, payload) {
    state.selectedClients = payload
  },
  setInputTxValue(state, {ticker, ...values}) {
    state.inputTransactions = {
      ...state.inputTransactions,
      [ticker]: {
        ...state.inputTransactions[ticker],
        ...values,
      },
    }
  },
  setTransactionDate(state, payload) {
    state.transactionDate = payload
  },
  setTransactionStartId(state, payload) {
    state.transactionStartId = payload
  },
}

const actions = {
  loadData({commit}) {
    return Promise
      .all([
        portfolioService.getTotalPortfolios(),
        priceService.listPrices(),
        userService.getClients(0, 1000),
      ])
      .then(([{assets: ownedAssets}, allAssets, {users: clients}]) => {
        commit('setOwnedAssets', ownedAssets)
        commit('setAllAssets', allAssets)
        commit('setClients', clients)
        return Promise.all(
          clients.map(({portfolios: [portfolio]}) =>
            portfolioService.getPortfolioDetails(portfolio)
          ))
      })
      .then((portfolios) => {
        const clientsAssets = portfolios.reduce((map, {userId: id, assets}) => {
          map[id] = assets.reduce((map, {ticker, pieces}) => {
            map[ticker] = pieces
            return map
          }, {})
          return map
        }, {})
        commit('setClientsAssets', clientsAssets)
      })
      .catch(errObj => Promise.reject(errObj.error))
  },
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
}



